import localStorage from './localStorage';

let expiredTime;

export default {
    set(authorizationExpiredRefreshTime, exdays = 1) {
        expiredTime = authorizationExpiredRefreshTime;
        localStorage.set('authorizationExpiredRefreshTime', authorizationExpiredRefreshTime);
    },
    get() {
        return expiredTime || localStorage.get('authorizationExpiredRefreshTime');
    },
    erase() {
        this.set('');
    },
};
