<script>
import queryString from 'query-string';
import innerLocalStorage from '../localStorage';
import refreshtokenStorage from '../refreshtokenStorage';
import authorizationExpiredTime from '../authorizationExpiredTime';
import cookie from '../cookie';
import token from '../token';
import { getArr, getObj, getTenant, NUIMS_DOMAIN_NAME } from '../util';
import service from '../service';
import { concatPluginList, usePlugin, loadOneRemotePlugin } from '../plugin/index';

export default {
    name: 'app',
    props: {
        nuimsUrl: {
            type: String,
            default: '',
        },
        tenantName: {
            type: String,
            default: '',
        },
        env: {
            type: String,
            default: '',
        },
        scene: {
            type: String,
            default: '',
        },
    },
    data() {
        return { AuthConfigMap: {} };
    },
    methods: {
        async oldMounted() {
            const { origin, search } = location;
            const query = queryString.parse(search);
            const { redirect, code, userName, userId, env, successUrl, span, refreshtoken, authorizationExpiredRefreshTime, scene  } = query;
            // 其他应用单点登录跳转
            if (userName && userId && code) {
                token.set(code);
                if (refreshtoken) {
                    refreshtokenStorage.set(refreshtoken)
                }
                if (authorizationExpiredRefreshTime) {
                    authorizationExpiredTime.set(authorizationExpiredRefreshTime)
                }
                innerLocalStorage.set('LoginType', 'Other');
                cookie.set({ LoginType: 'Other' });
                // eslint-disable-next-line require-atomic-updates
                location.href = redirect ? decodeURIComponent(redirect) : `${origin}/fenuims`;
            } else if (span && span === 'shufan_nologin') {
                try {
                    const res = await service.NuimsLogin({
                        url: this.nuimsUrl,
                        body: {
                            LoginType: 'Shufan',
                            TenantName: this.tenantName || getTenant(),
                            DomainName: NUIMS_DOMAIN_NAME,
                        },
                        headers: {
                            'x-proxy-source': (this.scene || scene) === 'LowcodePlatform' ? 'low-code' : '',
                        },
                    });
                    const { Data, headers } = getObj(getObj(res).data);
                    const { UserName, UserId } = getObj(Data);
                    const href = decodeURIComponent(redirect);
                    const Token = token.get();
                    let refreshPart = ''
                    const refreshtoken = refreshtokenStorage.get()
                    if (refreshtoken) {
                        refreshPart += `&refreshtoken=${refreshtoken}`
                    }
                    const authorizationExpiredRefreshTime = authorizationExpiredTime.get()
                    if (authorizationExpiredRefreshTime) {
                        refreshPart += `&authorizationExpiredRefreshTime=${authorizationExpiredRefreshTime}`
                    }
                    const Back = `${href}${
                        href.includes('?') ? '&' : '?'
                    }userName=${UserName}&userId=${UserId}&code=${Token}${refreshPart}`;
                    // eslint-disable-next-line require-atomic-updates
                    location.href = Back;
                } catch (error) {
                    console.error('NuimsLogin error: ');
                    if (+error.status === 403) {
                        // eslint-disable-next-line require-atomic-updates
                        location.href = redirect;
                    }
                }
            } else {
                // Auth 登录中转页
                const LoginType = innerLocalStorage.get('LoginType') || cookie.get('LoginType');
                const RedirectUri = innerLocalStorage.get('RedirectUri') || cookie.get('RedirectUri');
                const p = {
                    url: this.nuimsUrl,
                    params: { TenantName: this.tenantName || getTenant() },
                };
                if (this.env) {
                    p.headers = {
                        Env: this.env,
                    };
                }
                const res = await service.getTenantLoginTypes(p);
                const getResData = (res) => getArr(getObj(getObj(res).data).Data);
                const item = getResData(res).find((it) => it.LoginType === LoginType.slice(1));
                await loadOneRemotePlugin(item.LoginType, item.extendProperties.authJsUrl);
                let params = {
                    Code: code,
                    RedirectUri,
                    OAuthLoginType: 'Netease',
                    LoginType: LoginType.slice(1),
                    TenantName: getTenant(),
                    DomainName: NUIMS_DOMAIN_NAME,
                };
                let oAuthService = service.OauthLogin;
                if (LoginType === 'Github' || LoginType === 'CGithub') {
                    params.OAuthLoginType = 'Github';
                } else if (LoginType === 'Wechat' || LoginType === 'CWechat') {
                    params.OAuthLoginType = 'Wechat';
                // } else if (LoginType === 'CKeycloak' || LoginType === 'Keycloak') {
                //     params.OAuthLoginType = 'Keycloak';
                } else if (LoginType === 'CTaikang' || LoginType === 'Taikang') {
                    await this.getTenantConfig();
                    params.OAuthLoginType = 'Taikang';
                    oAuthService = service.NuimsLogin;
                    params = {
                        url: this.nuimsUrl,
                        body: {
                            Code: code,
                            RedirectUri,
                            LoginType: 'Taikang',
                            TenantName: this.tenantName || getTenant(),
                            DomainName: NUIMS_DOMAIN_NAME,
                        },
                        headers: {
                            Env: this.env,
                            'x-proxy-source': (this.scene || scene) === 'LowcodePlatform' ? 'low-code' : '',
                        },
                    };
                } else if (LoginType === 'icbc_aam' || LoginType === 'Cicbc_aam') {
                    // 工行登陆
                    const { SSIAuth, SSISign } = query;
                    oAuthService = service.NuimsLogin;
                    params = {
                        url: this.nuimsUrl,
                        body: {
                            // Code: code,
                            SsiAuth: SSIAuth,
                            SsiSign: SSISign,
                            RedirectUri,
                            LoginType: 'icbc_aam',
                            TenantName: this.tenantName || getTenant(),
                            DomainName: NUIMS_DOMAIN_NAME,
                        },
                        headers: {
                            Env: this.env,
                            'x-proxy-source': (this.scene || scene) === 'LowcodePlatform' ? 'low-code' : '',
                        },
                    };
                } else if (LoginType === 'Shufan' || LoginType === 'CShufan') {
                    oAuthService = service.NuimsLogin;
                    params = {
                        url: this.nuimsUrl,
                        body: {
                            // Code: code,
                            RedirectUri,
                            LoginType: 'Shufan',
                            TenantName: this.tenantName || getTenant(),
                            DomainName: NUIMS_DOMAIN_NAME,
                        },
                        headers: {
                            Env: env || this.env,
                            'x-proxy-source': (this.scene || scene) === 'LowcodePlatform' ? 'low-code' : '',
                        },
                    };
                }

                const context = {
                    AuthConfigMap: this.AuthConfigMap,
                    loginType: LoginType.slice(1),
                    query: queryString.parse(search),
                    cookie,
                    RedirectUri,
                };
                const callback = (paramsCb, oAuthServiceCb) => {
                    params = {
                        url: paramsCb?.url || this.nuimsUrl,
                        body: {
                            TenantName: this.tenantName || getTenant(),
                            DomainName: NUIMS_DOMAIN_NAME,
                            ...params,
                            ...paramsCb?.body,
                        },
                        headers: {
                            Env: paramsCb?.env || env || this.env,
                            'x-proxy-source': (this.scene || scene) === 'LowcodePlatform' ? 'low-code' : '',
                        },
                    };
                    oAuthService = oAuthServiceCb || service.NuimsLogin;
                };
                usePlugin('nuimsCodeLogin', context, callback);

                try {
                    const res = await oAuthService(params);
                    const { Data } = getObj(getObj(res).data);
                    const { UserName, UserId } = getObj(Data);
                    let href;
                    // 'Keycloak',
                    const pluginList = ['icbc_aam', 'Github', 'Wechat', 'icbc_aam', 'Taikang', 'Shufan'];
                    // 登录组件通过 Auth 登录中转页
                    if (concatPluginList(pluginList).includes(LoginType.slice(1))) {
                        href = decodeURIComponent(
                            redirect || innerLocalStorage.get('From') || cookie.get('From'),
                        );
                        const Token = token.get();
                        let refreshPart = ''
                        const refreshtoken = refreshtokenStorage.get()
                        if (refreshtoken) {
                            refreshPart += `&refreshtoken=${refreshtoken}`
                        }
                        const authorizationExpiredRefreshTime = authorizationExpiredTime.get()
                        if (authorizationExpiredRefreshTime) {
                            refreshPart += `&authorizationExpiredRefreshTime=${authorizationExpiredRefreshTime}`
                        }
                        const Back = `${href}${
                            href.includes('?') ? '&' : '?'
                        }userName=${UserName}&userId=${UserId}&code=${Token}&loginType=${LoginType.slice(1)}${refreshPart}`;
                        href = Back;
                    } else {
                        href = redirect ? decodeURIComponent(redirect) : `${origin}/fenuims`;
                    }
                    location.href = href;
                } catch (error) {
                    const { message } = error || {};
                    if (message && message.includes('The user is not bound')) {
                        location.href = '/403';
                    } else {
                        this.$toast.show(message);
                    }
                }
            }
        },
        async getTenantConfig() {
            const AuthConfigMap = {};
            const params = {
                url: this.nuimsUrl,
                params: { TenantName: this.tenantName || getTenant() },
            };
            if (this.env) {
                params.headers = {
                    Env: this.env,
                };
            }
            const res = await service.getTenantLoginTypes(params);
            getArr(getObj(getObj(res).data).Data).forEach((d) => {
                const fullConfig = getObj(d);
                // eslint-disable-next-line no-case-declarations
                const { LoginType } = fullConfig;
                AuthConfigMap[LoginType] = fullConfig;
            });
            this.AuthConfigMap = AuthConfigMap;
        },
    },
    render(h) {
        return h('div', '');
    },
};
</script>

