const aesKey = ';Z#^$;8+yhO!AhGo';

// AES ECB模式加密
export function AES_ECB_encrypt(message, key = aesKey) {
    // utf8字符串—>WordArray对象，WordArray是一个保存32位整数的数组，相当于转成了二进制
    const keyHex = CryptoJS.enc.Utf8.parse(key); //
    const messageHex = CryptoJS.enc.Utf8.parse(message);
    const encrypted = CryptoJS.AES.encrypt(messageHex, keyHex, {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7,
    });
    return encrypted.toString();// base64结果
    // return encrypted.ciphertext.toString();   // 二进制结果
}

export function parseToken(token) {
    const strings = token.split('.');
    const userinfo = JSON.parse(
        decodeURIComponent(
            escape(
                window.atob(strings[1].replace(/-/g, '+')
                    .replace(/_/g, '/')),
            ),
        ),
    );
    return userinfo;
}

export function loadScript(src, root = document.body) {
    return new Promise((resolve, reject) => {
        const script = document.createElement('script');
        script.setAttribute('src', src);
        script.addEventListener('load', () => resolve(), false);
        script.addEventListener('error', () => reject(new Error('js asset loaded error: ' + src)));
        root.appendChild(script);
    });
}

export function getLoginHref(redirect) {
    if (window.KeycloakConfig) {
        const { protocol, host } = location;
        return `${window.KeycloakConfig.extendProperties.logoutUrl}?redirect_uri=${protocol}//${host}/login`;
    } else {
        const { protocol } = location;
        const { consoleDomain } = window.appInfo || {};
        const loginUrl = consoleDomain ? `${protocol}//${consoleDomain}/login` : '/login';
        const redirectQuery = redirect ? `?redirect=${redirect}` : '';
        return `${loginUrl}${redirectQuery}`;
    }
}
