class Ehr {
    constructor() {
        this.name = 'Ehr';
        this.category = 'Sub';
    }

    jumpPass(context, callback) {
        const { authUrl, queryString, redirectUri } = context;
        const authObj = new URL(authUrl);
        const obj = queryString.parse(authObj.search);
        obj.returnUrl = redirectUri;
        obj.errUrl = redirectUri;
        authObj.search = queryString.stringify(obj);
        callback(authObj.toString());
    }

    nuimsJumpPass(context, callback) {
        const { authUrl, queryString, redirectUri } = context;
        const authObj = new URL(authUrl);
        const obj = queryString.parse(authObj.search);
        obj.returnUrl = redirectUri;
        obj.errUrl = redirectUri;
        authObj.search = queryString.stringify(obj);
        callback(authObj.toString());
    }
}
export default Ehr;
