<template>
    <div>
        <nuims-cb ref="NuimsCb" :env="env" :nuims-url="'nuims'" :tenant-name="tenantName" :scene="scene"></nuims-cb>
        <nuims-pass ref="NuimsPass" :env="env" :nuims-url="'nuims'" :tenant-name="tenantName" :scene="scene"></nuims-pass>
        <div v-if="compType === 'pass'">
            <div :class="$style.loading">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                请稍后...
            </div>
        </div>
        <div v-else-if="compType === 'cb'">
            <div :class="$style.loading">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                登录中
            </div>
        </div>
        <div v-else-if="!isSzhz&&enableUI" :class="$style.root" :style="{height:authTypes.length>0?'393px':'317px',
            boxShadow:loading?'0 0 0 #fff':'0 0 10px 0 rgba(80, 90, 109, 0.16)'}">
            <u-linear-layout
                v-if="loading"
                type="flex"
                justify="center"
                alignment="center"
                :class="$style.loading"
            >
                <u-spinner></u-spinner>加载中...
            </u-linear-layout>
            <div v-else-if="hasTabLoginTypes" :class="$style.form">
                <u-linear-layout direction="vertical" gap="large" style="padding-bottom:35px;">
                    <!-- tabs（主登录） tabs-->
                    <u-tabs v-model="account.LoginType" appear="line">
                        <u-tab
                            v-for="tab in tabs"
                            :key="tab.value"
                            :title="tab.title"
                            :value="tab.value"
                        ></u-tab>
                    </u-tabs>
                    <!-- 账号输入框 -->
                    <u-linear-layout type="flex" justify="center" style="margin-bottom:24px">
                        <u-validator
                            ref="accValidator"
                            placement="bottom"
                            style="max-width: 100%"
                            :rules="accountRule"
                        >
                            <u-input
                                style="width:330px;height:40px"
                                size="large"
                                name="account"
                                v-model="account.AccountId"
                                placeholder="请输入账号"
                                maxlength="128"
                                maxlength-message="账号最多输入128个字符"
                                prefix="user"
                                clearable
                                @change="rmErrMsg"
                            >
                                <span name="prefix" :class="$style.prefix">
                                    <icon-custom name="user"></icon-custom>
                                </span>
                            </u-input>
                        </u-validator>
                    </u-linear-layout>
                    <!-- 密码输入框 -->
                    <u-linear-layout type="flex" justify="center" style="margin-bottom:30px">
                        <u-validator
                            ref="pwdValidator"
                            placement="bottom"
                            style="max-width: 100%"
                            :rules="pwdRule"
                        >
                            <u-input
                                style="width:330px;height:40px"
                                size="large"
                                name="password"
                                :type="showPassword ? 'text' : 'password'"
                                v-model="account.AccountPassword"
                                @keyup.enter="login"
                                @change="rmErrMsg"
                                placeholder="请输入密码"
                                maxlength="128"
                                maxlength-message="密码最多输入128个字符"
                                prefix="lock"

                            >
                                <span name="prefix" :class="$style.prefix">
                                    <icon-custom name="lock"></icon-custom>
                                </span>
                                <span
                                    name="suffix"
                                    :class="$style.suffix"
                                    @click="showPassword = !showPassword"
                                >
                                    <icon-custom
                                        :name="showPassword ? 'eye-close' : 'eye'"
                                    ></icon-custom>
                                </span>
                            </u-input>
                        </u-validator>
                    </u-linear-layout>
                    <!-- 登录按钮 -->
                    <u-linear-layout direction="vertical" gap="small">
                        <u-button
                        size="large"
                            color="primary"
                            display="block"
                            @click="login"
                            :icon="loading ? 'loading' : undefined"
                            :disabled="loading"
                        >登&nbsp;录</u-button>
                    </u-linear-layout>
                    <div v-if="authTypes.length > 0" :class="$style.dividerbox">
                        <div :class="$style.divider"></div>
                        <span style="margin:0 18px;">或</span>
                        <div :class="$style.divider"></div>
                    </div>
                    <!-- 第三方登录方式（辅助登录） authTypes -->
                        <!-- v-if="isIDE && !useRedirect" -->
                    <u-linear-layout
                        type="flex"
                        alignment="center"
                    >
                        <div :class="$style.linkbox" v-for="auth in authTypes" :key="auth.value">
                            <u-link
                                @click="jumpAuth(auth.value)"
                                :class="$style.link"
                            >{{ auth.title }}
                            </u-link>
                        </div>
                    </u-linear-layout>
                </u-linear-layout>
            </div>
            <div v-else-if="!loading" :class="$style.links">
                <u-link
                    v-for="auth in authTypes"
                    :key="auth.value"
                    @click="jumpAuth(auth.value)"
                    :class="$style.link"
                >{{ auth.title }}</u-link>
                <span v-if="showNoneTips" :class="$style.tips">请确认登录配置</span>
            </div>
        </div>
        <div v-else-if="isSzhz">
            <iframe :class="$style.myiframe" width="200" height="300" frameborder="no"
             border="0" marginwidth="0" marginheight="0" scrolling="no" src="https://login-pro.ding.zj.gov.cn/qrlogin/webAppLogin.htm?APP_NAME=szhzmh_dingoa&protocolKey=78781524-d5be-4430-8bdb-cf70553be44f&protocol=oauth2&BACK_URL=http://dev.jzx0328.defaulttenant.lcap.hatest.163yun.com/&embedMode=true&scope=get_user_info&state=">
            </iframe>
        </div>
    </div>
</template>

<script>
import queryString from 'query-string';
import iconCustom from './icon-custom.vue';
import cookie from './cookie';
import service from './service';
import './iconfont.js';
import tokenMethod from './token';
import { getLoginHref, NUIMS_DOMAIN_NAME, getArr, getTenant, rmLastSlash, getObj, authKeyLoginType, AES_ECB_encrypt, parseToken } from './util';
import localService from './usercenter/service';
import { concatPluginList, usePlugin, loadRemotePlugins } from './plugin/index';
import refreshtokenStorage from './refreshtokenStorage';
import authorizationExpiredTime from './authorizationExpiredTime';

import NuimsCb from './nuims-cb/nuims-cb.vue';
import NuimsPass from './nuims-pass/nuims-pass.vue';

const newDomain = location.host.split('.').includes('163');
const serviceMap = {
    generateSfToken: 'https://sfsso.community1.lcap.qz.163yun.com/api/generateSfToken',
    generateSfTokenNew: 'https://sfsso-community1.app.codewave.163.com/api/generateSfToken',
    checkSfToken: `${location.protocol}//sfsso.community1.lcap.qz.163yun.com/api/checkSfToken`,
    checkSfTokenNew: `${location.protocol}//sfsso-community1.app.codewave.163.com/api/checkSfToken`,
};
let appinfoEnv; let src;
if (window?.appInfo?.env) {
    appinfoEnv = window.appInfo.env;
}
if (window?.appInfo?.nuimsDomain) {
    const tenant = getTenant();
    const tenantUri = `${tenant}.`;
    src = `${location.protocol}//dev.${tenantUri}${window.appInfo.nuimsDomain}`;
}
const Keycloak = 'Keycloak';
const isPlatform = (text) => ['LowcodePlatform', 'PermissionCenter'].includes(text);
const debounce = (fn, ms = 0) => {
    let timeoutId;
    return function (...args) {
        clearTimeout(timeoutId);
        timeoutId = setTimeout(() => fn.apply(this, args), ms);
    };
};
export default {
    name: 'lcap-login',
    components: { iconCustom, NuimsCb, NuimsPass },
    props: {
        scene: {
            type: String,
            default: '',
        },
        displayMode: {
            type: Boolean,
            default: false,
        },
        useRedirect: {
            type: Boolean,
            default: true,
        },
        mainLoginTypes: {
            type: Array,
            default: () => [],
        },
        redirectLoginTypes: {
            type: Array,
            default: () => [],
        },
        subLoginTypes: {
            type: Array,
            default: () => [],
        },
        // 账号认证与权限中心域名
        src: {
            type: String,
            default: src,
        },
        // 账号认证与权限中心请求相对路径
        nuimsUrl: {
            type: String,
            default: '/gateway/nuims/nuims',
        },
        // 账号认证与权限中心环境信息
        env: {
            type: String,
            default: appinfoEnv,
        },
        // 租户名称
        tenantName: String,
        // 登录应用名称
        domainName: String,
        // 开启普通登录，默认开启
        useNormal: {
            type: Boolean,
            default: true,
        },
        // 开启轻舟登录
        useQZ: {
            type: Boolean,
            default: false,
        },
        // 开启 LDAP 登录
        useLdap: {
            type: Boolean,
            default: false,
        },
        // 开启 OpenID 登录
        useNetease: {
            type: Boolean,
            default: false,
        },
        // 开启 Github 登录
        useGithub: {
            type: Boolean,
            default: false,
        },
        // 开启微信登录
        useWechat: {
            type: Boolean,
            default: false,
        },
        // 开启工行登录
        useicbc_aam: {
            type: Boolean,
            default: false,
        },
        useXuetong: {
            type: Boolean,
            default: true,
        },
        useTaikang: {
            type: Boolean,
            default: false,
        },
        useKeycloak: {
            type: Boolean,
            default: false,
        },
        exdays: {
            type: Number,
            default: 1,
        },
        // 开启在制品里面的登录
        hasUserCenter: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            isSzhz: false,
            jsUrls: {},
            isDirectNoUI: false,
            loading: true,
            account: {
                LoginType: 'Normal',
                AccountId: '',
                AccountPassword: '',
                isplugin: false,
            },
            AuthConfigMap: {},
            tabs: [],
            authTypes: [
            ],
            hasTabLoginTypes: true,
            showNoneTips: false,
            showPassword: false,
            errMsg: '',
            accountRule: ['required'],
            pwdRule: ['required'],
            low: this.hasUserCenter, // 控制下沉模式（制品应用内部登录） 还是 集中模式（nuims集中登录）
            compType: localStorage.getItem('compType'),
        };
    },
    computed: {
        indexPage() {
            let url = '/';
            if (window?.appInfo?.basePath) {
                url = `${window.appInfo.basePath}/`;
            }
            if (Vue.prototype.$formatMicroFrontRouterPath)
                url = Vue.prototype.$formatMicroFrontRouterPath(url);

            return url;
        },
        enableUI() {
            if (this.isIDE) {
                return true;
            }
            return this.useRedirect;
        },
        isIDE() {
            return this?.$env?.VUE_APP_DESIGNER;
        },
        isProductApp() {
            if (this.isIDE) {
                return false;
            }
            return window.appVue;
        },
        fnEnv() {
            if (this.isProductApp)
                return window?.appInfo?.env;
            return window?.appInfo?.platformEnv || this.env;
        },
        passSrc() {
            if (this.isProductApp) {
                if (window?.appInfo?.nuimsDomain) {
                    const tenant = getTenant();
                    const tenantUri = `${tenant}.`;
                    if (window?.appInfo?.env && window?.appInfo?.env === 'online') {
                        src = `${location.protocol}//${tenantUri}${window.appInfo.envNuimsDomain.online}`;
                    } else {
                        src = `${location.protocol}//dev.${tenantUri}${window.appInfo.envNuimsDomain.dev}`;
                    }
                }
                return `${rmLastSlash(src)}`;
            }
            if (this.scene === 'LowcodePlatform') {
                if (window?.appInfo?.nuimsDomain) {
                    const tenant = getTenant();
                    const tenantUri = `${tenant}.`;
                    const platformEnv = window?.appInfo?.platformEnv;
                    if (platformEnv === 'online') {
                        src = `${location.protocol}//${tenantUri}${window.appInfo.nuimsDomain}`;
                    } else {
                        // src = `${location.protocol}//dev.${tenantUri}${window.appInfo.nuimsDomain}`;
                        src = `http://dev.${tenantUri}${window.appInfo.nuimsDomain}`;
                    }
                }
                return `${rmLastSlash(src)}`;
            }
            return `${rmLastSlash(this.src)}`;
        },
    },
    watch: {
        useRedirect(val) {
            // 在ide里 即使关闭UI按钮 也要展示 UI
            if (this.isIDE && !val) {
                setTimeout(() => {
                    this.resetToDefault();
                }, 500);
            }
        },
        mainLoginTypes(val) {
            (isPlatform(this.scene) || this.isIDE) && this.initUI(this.mainLoginTypes, this.subLoginTypes, this.redirectLoginTypes);
        },
        subLoginTypes(val) {
            (isPlatform(this.scene) || this.isIDE) && this.initUI(this.mainLoginTypes, this.subLoginTypes, this.redirectLoginTypes);
        },
        redirectLoginTypes(val) {
            (isPlatform(this.scene) || this.isIDE) && this.initUI(this.mainLoginTypes, this.subLoginTypes, this.redirectLoginTypes);
        },
    },
    async mounted() {
        this.dealFreesassLoginDebounce = debounce(this.dealFreesassLogin, 200);

        this.getQrcodeMessage();
        const { search } = location;
        const query = queryString.parse(search);
        let { role, loginType, code, authToken, redirect, userName, userId, token, authKey, externalSystem, span, shufanAuthPageUrl, loginSuccessUrl, refreshtoken, authorizationExpiredRefreshTime, scene } = query;

        if (refreshtoken) {
            refreshtokenStorage.set(refreshtoken)
        }
        if (authorizationExpiredRefreshTime) {
            authorizationExpiredTime.set(authorizationExpiredRefreshTime)
        }
        const { consoleDomain } = window.appInfo || {};
        if (consoleDomain && role !== 'admin') {
            const loginHref = getLoginHref();
            location.href = loginHref;
            return;
        }
        this.low = window?.appInfo?.hasUserCenter || false;
        if (this.displayMode) {
            this.loading = false;
            return;
        }
        loginType = localStorage.getItem('LoginType') || cookie.get('LoginType') || loginType;
        if (!this.isIDE) {
            await this.loadRemote();
        }
        if (this.compType === 'pass') {
            // 制品应用不应该 走接口
            if (this.isProductApp) {
                await this.initProductAppUI('pass');
            } else {
                await this.getTenantConfig();
            }
            try {
                this.jumpAuthPass();
            } catch (error) {
                console.error('error: ', error);
                localStorage.setItem('compType', '');
            }
        } else if (this.compType === 'cb') {
            localStorage.setItem('compType', '');
            try {
                if (this.isProductApp) {
                    await this.initProductAppUI('cb');
                } else {
                    await this.getTenantConfig();
                }
                await this.codeLogin(query);
            } catch (error) {
                location.reload();
                console.error('error: ', error);
            }
        } else {
            if (authToken && redirect) {
                cookie.set({ authorization: authToken });
                this.$emit('success', {
                    Authorization: authToken,
                });
                location.href = redirect;
            } else if (token) {
                // 对返回结果只包含 token 的解析方式，，目前主要是对接学同的场景
                cookie.set({ authorization: token });
                this.$emit('success', {
                    Authorization: token,
                });
                if (window.appVue) {
                    location.href = this.indexPage;
                }
            } else if (userName && userId && code) {
                const cookieUserInfo = parseToken(code);
                if (cookieUserInfo.AuthTypeTokenExpire) {
                    const day = +cookieUserInfo.AuthTypeTokenExpire / 60 / 60 / 24;
                    cookie.set({ authorization: code }, day);
                } else {
                    cookie.set({ authorization: code });
                }
                let href = loginSuccessUrl || this.indexPage;
                if (loginSuccessUrl) {
                    // emit success 到低代码平台时无法带参 这里直接截断跳转吧
                    location.href = loginSuccessUrl;
                    return;
                }
                // cb 回来才有 logintype=shufan
                if (['Shufan', 'CShufan'].includes(query.loginType)) {
                    let url = serviceMap.generateSfToken;
                    if (newDomain) {
                        url = serviceMap.generateSfTokenNew;
                    }
                    const res = await fetch(url, {
                        method: 'POST',
                        mode: 'cors',
                        credentials: 'include',
                    });
                    const data = await res.json();
                    const tokenInfo = data?.Data;
                    if (tokenInfo) {
                        // tokenInfo.expires = tokenInfo.expires || 15;
                        // expires=${getExpires(tokenInfo.expires)};
                        document.cookie = `${tokenInfo.name}=${tokenInfo.value};domain=${tokenInfo.domain};path=${tokenInfo.cookiePath}; `;
                    }
                    const shufanPage = localStorage.getItem('shufanPage');
                    tokenMethod.set(code);
                    localStorage.setItem('LoginType', 'Other');
                    cookie.set({ LoginType: 'Other' });
                    if (shufanPage) {
                        location.href = shufanPage;
                        return;
                    }
                }
                const context = {
                    loginType,
                    Authorization: code,
                    UserName: userName,
                    UserId: userId,
                };
                const callback = (hrefCb) => {
                    if (hrefCb) {
                        href = hrefCb;
                    }
                };
                usePlugin('afterLogin', context, callback);
                this.$emit('success', {
                    Authorization: code,
                    UserName: userName,
                    UserId: userId,
                    href,
                });
                if (window.appVue && !this.$listeners.success) {
                    location.href = href;
                }
            } else if (authKeyLoginType.includes(externalSystem)) {
                await service.ExternalSystemLogin({
                    url: this.nuimsUrl,
                    body: {
                        externalSystem,
                        data: {
                            authKey,
                        },
                    },
                });

                const Token = tokenMethod.get();
                if (Token) {
                    this.jumpSuccess(Token);
                }
            } else if (span && span === 'shufan_nologin') {
                try {
                    cookie.erase('authorization');
                    const res = await service.NuimsLogin({
                        url: this.nuimsUrl,
                        body: {
                            LoginType: 'Shufan',
                            TenantName: this.tenantName || getTenant(),
                            DomainName: NUIMS_DOMAIN_NAME,
                        },
                        headers: {
                            'x-proxy-source': (this.scene || scene) === 'LowcodePlatform' ? 'low-code' : '',
                        },
                    });
                    const Token = res?.headers?.authorization;
                    if (Token) {
                        cookie.set({ authorization: Token });
                        location.href = `/sso?token=${Token}`;
                    }
                    if (res.headers?.refreshtoken) {
                        refreshtokenStorage.set(res.headers?.refreshtoken)
                    }
                    if (res?.headers?.['max-age']) {
                        let maxAge = Number(res?.headers['max-age'] || 0)
                        if (maxAge > 200) {
                            maxAge -= 200
                        }
                        const expiredTime = Date.parse(new Date()) / 1000 + maxAge
                        authorizationExpiredTime.set(expiredTime)
                    }
                } catch (error) {
                    if (+error.status === 403) {
                        location.href = shufanAuthPageUrl;
                    }
                }
            } else {
                if (this.isProductApp) {
                    // 制品应用 从nasl 获取 数据
                    await this.initProductAppUI();
                    this.loading = false;
                } else if (isPlatform(this.scene) || this.isIDE) {
                    // 平台应用 从接口 获取 数据
                    await this.getTenantConfig();
                }
            }
        }
    },
    methods: {
        getQrcodeMessage() {
            const messageEventFn = async (event) => {
                const { origin, data } = event;
                if (origin === 'https://login-pro.ding.zj.gov.cn') {
                    const { code } = data;
                    let res;
                    this.low = window?.appInfo?.hasUserCenter || false;

                    if (this.low) {
                        const params = {
                            data: {
                                LoginType: 'Szhz',
                                TenantName: this.tenantName || getTenant(),
                                DomainName: NUIMS_DOMAIN_NAME,
                                code,
                                Code: code,
                                qrCode: code,
                                QrCode: code,
                            },
                        };
                        res = await localService.login(params);
                    } else {
                        const params = {
                            url: this.nuimsUrl,
                            body: {
                                LoginType: 'Szhz',
                                TenantName: this.tenantName || getTenant(),
                                DomainName: NUIMS_DOMAIN_NAME,
                                code,
                                Code: code,
                                qrCode: code,
                                QrCode: code,
                            },
                            headers: {
                                'x-proxy-source': this.scene === 'LowcodePlatform' ? 'low-code' : '',
                            },
                        };
                        res = await service.NuimsLogin(params);
                    }

                    const { userId, userName, UserId, UserName } = res.data.Data;
                    const { authorization } = res.headers;
                    cookie.set({ authorization });
                    if (res.headers?.refreshtoken) {
                        refreshtokenStorage.set(res.headers?.refreshtoken)
                    }
                    if (res?.headers?.['max-age']) {
                        let maxAge = Number(res?.headers['max-age'] || 0)
                        if (maxAge > 200) {
                            maxAge -= 200
                        }
                        const expiredTime = Date.parse(new Date()) / 1000 + maxAge
                        authorizationExpiredTime.set(expiredTime)
                    }
                    this.$emit('success', { Authorization: authorization,
                                            UserName: UserName || userName,
                                            UserId: UserId || userId,
                                            LoginType: 'Szhz' });
                    if (window.appVue && !this.$listeners.success) {
                        location.href = this.indexPage;
                    }
                }
            };
            if (typeof window !== 'undefined') {
                window.addEventListener('message', messageEventFn);
            }
        },
        async loadRemote() {
            let jsUrls;
            const getResData = (res) => getArr(getObj(getObj(res).data).Data);
            const formatJsUrl = (list) => getResData(list).filter((it) => it.extendProperties)
                .map((it) => ({ JsUrl: it.extendProperties?.authJsUrl?.replace(/^https?:/i, ''), Type: it.LoginType }));
            if (this.low) {
                const res = await localService.getTenantLoginTypes({});
                const Data = getObj(getObj(res).data).Data;
                jsUrls = Object.keys(Data).map((key) => {
                    const it = Data[key];
                    if (it && it.config && it.config.authJsUrl) {
                        return {
                            JsUrl: it.config.authJsUrl,
                            Type: key,
                        };
                    } else {
                        return { JsUrl: '', Type: '' };
                    }
                });
            } else {
                const p = {
                    url: this.nuimsUrl,
                    params: { TenantName: this.tenantName || getTenant() },
                };
                if (this.env) {
                    p.headers = {
                        Env: this.env,
                    };
                }
                const res = await service.getTenantLoginTypes(p);
                jsUrls = formatJsUrl(res);
            }
            await loadRemotePlugins(jsUrls);
        },
        nuimsPassLogic() {
            !this.isProductApp && this.$refs.NuimsPass.oldMounted();
        },
        nuimsCbLogic() {
            !this.isProductApp && this.$refs.NuimsCb.oldMounted();
        },
        resetToDefault() {
            this.tabs = [{
                title: '普通登录',
                value: 'Normal',
            }];
            this.authTypes = [];
        },
        async initProductAppUI(scene) {
            const format = (item) => ({
                value: item.name,
                ...item,
            });
            this.tabs = this.mainLoginTypes.filter((item) => item.default).map(format);
            const isTzbank = this.tabs.findIndex((it) => it.value === 'TZBank');
            if (isTzbank) {
                // this.tabs = []
            }

            this.authTypes = this.subLoginTypes.filter((item) => item.default).map(format);
            const isSzhz = this.authTypes.findIndex((it) => it.value === 'Szhz') >= 0;
            this.isSzhz = isSzhz;
            const obj = this.authTypes.find((item) => (item.value === Keycloak));
            if (obj && !this.displayMode && !scene) {
                // this.jumpAuth(Keycloak);
            }
            const direct = this.redirectLoginTypes[0];
            if (!this.mainLoginTypes || this.mainLoginTypes?.length <= 0) {
                this.resetToDefault();
            }
            const hasGonghang = [...this.mainLoginTypes, ...this.subLoginTypes].some((item) => (item.value === 'icbc_aam' || item.name === 'icbc_aam' || item.name === 'icbc_aam'));
            const isicbc_aam = this.useicbc_aam || hasGonghang;
            if (isicbc_aam && !this.displayMode && !scene) {
                // 开启 icbc_aam 默认跳转 icbc_aam
                this.jumpAuth('icbc_aam');
            }
            this.defaultFocus();
            if (direct && !this.useRedirect) {
                this.isDirectNoUI = true;

                if (direct.name === 'Netease' && !scene) {
                    this.jumpAuth('Netease');
                }
                if (direct.name === 'Netease1' && !scene) {
                    this.jumpAuth('Netease1');
                }
                if (direct.name === 'Xuetong' && !scene) {
                    this.jumpAuth('Xuetong');
                }
                if (direct.name === 'Keycloak' && !scene) {
                    this.jumpAuth('Keycloak');
                }
                if (direct.name === 'Ehr' && !scene) {
                    this.jumpAuth('Ehr');
                }
                if (direct.name === 'Oscar' && !scene) {
                    this.jumpAuth('Oscar');
                }

                const context = {
                    loginType: direct.name, localService,
                    service, scene,
                    low: this.low,
                    query: queryString.parse(location.search),
                    jumpAuthFn: this.jumpAuth,
                    compType: this.compType,
                    cookieUitl: cookie,
                    nuimsUrl: this.nuimsUrl,
                    TenantName: this.tenantName || getTenant(),
                    DomainName: NUIMS_DOMAIN_NAME,
                };
                const callback = (userInfoCb, hrefCb) => {
                    if (userInfoCb) {
                        const { authorization, UserName, UserId } = userInfoCb;
                        cookie.set({ authorization,
                                     userName: UserName,
                                     userId: UserId });
                        this.$emit('success', { Authorization: authorization, UserName, UserId, ...userInfoCb });
                        if (window.appVue && !this.$listeners.success) {
                            location.href = hrefCb || this.indexPage;
                        }
                    }
                };
                usePlugin('initDirectProduct', context, callback);
                // 因为数帆 没有回跳参数 只是同域名写cookie
                if (scene !== 'cb' && (direct.name === 'Shufan' || direct.value === 'Shufan')) {
                    await this.initShufan();
                }
            }
        },
        async initUI(mainLoginTypes, subLoginTypes, redirectLoginTypes) {
            const isActive = (item) => item.default === true;
            this.tabs = mainLoginTypes.filter(isActive).map((it) => ({
                ...it,
                value: it?.name?.replace('use', ''),
            }));
            this.authTypes = subLoginTypes.filter(isActive);
            const obj = this.authTypes.find((item) => (item.value === Keycloak));
            if (obj && !this.displayMode) {
                // this.jumpAuth(Keycloak);
            }
            if (this.isIDE && !this.useRedirect) {
                this.resetToDefault();
            }
            const direct = redirectLoginTypes[0];
            let useDirect = direct;
            if (this.isProductApp) {
                useDirect = direct && !this.useRedirect;
            }
            if (useDirect) {
                this.isDirectNoUI = true;

                if (direct.value === 'Netease') {
                    this.jumpAuth('Netease');
                }
                if (direct.value === 'Netease1') {
                    this.jumpAuth('Netease1');
                }
                if (direct.value === 'Xuetong') {
                    this.jumpAuth('Xuetong');
                }
                if (direct.value === 'Keycloak') {
                    this.jumpAuth('Keycloak');
                }

                if (direct.value === 'Shufan') {
                    await this.initShufan();
                }
                const context = {
                    loginType: direct.value,
                    service,
                    query: queryString.parse(location.search),
                    jumpAuthFn: this.jumpAuth,
                    compType: this.compType,
                    cookieUitl: cookie,
                    nuimsUrl: this.nuimsUrl,
                    TenantName: this.tenantName || getTenant(),
                    DomainName: NUIMS_DOMAIN_NAME,
                };
                const callback = (userInfoCb, hrefCb) => {
                    if (userInfoCb) {
                        const { authorization, UserName, UserId } = userInfoCb;
                        cookie.set({ authorization,
                                     userName: UserName,
                                     userId: UserId });
                        this.$emit('success', { Authorization: authorization, UserName, UserId });

                        location.href = hrefCb || this.indexPage;
                    }
                };
                usePlugin('initDirectPlatform', context, callback);
            }
            if (this.displayMode && this.tabs.length > 0) {
                this.account.LoginType = this.tabs[0].value;
            }
        },
        async getTenantConfig() {
            let authTypes = [];
            if (this.isIDE) {
                this.initUI(this.mainLoginTypes, this.subLoginTypes, this.redirectLoginTypes);
                if (this.tabs.length <= 0) {
                    this.tabs = [{
                        title: '普通登录',
                        value: 'Normal',
                    }];
                    authTypes = [];
                }
            } else {
                try {
                    let res;
                    if (this.low) {
                        res = await localService.getTenantLoginTypes({
                        });
                    } else {
                        const p = {
                            url: this.nuimsUrl,
                            params: { TenantName: this.tenantName || getTenant() },
                        };
                        if (this.env) {
                            p.headers = {
                                Env: this.env,
                            };
                        }
                        // res = await service.getTenantLoginTypes({
                        res = await service.GetTenantSortedLoginTypes(p);
                    }
                    const format = (item) => ({
                        title: item.LoginLabel,
                        name: item.LoginType,
                        value: item.LoginType,
                        default: true,
                        plugin: item.Plugin,
                    });
                    const resData = getObj(getObj(getObj(res).data).Data);

                    const mainLoginTypes = resData.Main.map(format);
                    this.mainLoginTypes = mainLoginTypes;
                    authTypes = resData.Sub.map(format);
                    this.subLoginTypes = authTypes;
                    const redirectLoginTypes = resData.Direct.map(format);
                    this.redirectLoginTypes = redirectLoginTypes;
                    this.initUI(mainLoginTypes, authTypes, redirectLoginTypes);
                } catch (error) {
                    console.info(error);
                    authTypes = [];
                }
            }
            if (this.tabs.length > 0) {
                this.account.LoginType = this.tabs[0].value;
            } else {
                this.hasTabLoginTypes = false;
                if (authTypes.length === 1) {
                    // 当有且仅有一种 Auth 登录时，直接登录
                    this.jumpAuth(authTypes[0].value);
                } else if (authTypes.length === 0) {
                    this.showNoneTips = true;
                }
            }
            this.loading = false;
        },
        async dealFreesassLogin(query) {
            const prod = 'community.lcap.qz.163yun.com';
            const hadri = 'community.lcap.hadri.163yun.com';
            const prodNew = 'community.codewave.163.com';
            const isCommity = location.href.includes(hadri) || location.href.includes(prod) || location.href.includes(prodNew);
            if (+window.appInfo?.tenantType === 1 || isCommity) {
                try {
                    // 校验接口
                    let url = serviceMap.checkSfToken;
                    if (newDomain) {
                        url = serviceMap.checkSfTokenNew;
                    }
                    const res = await fetch(url, {
                        method: 'POST',
                        mode: 'cors',
                        credentials: 'include',
                    });
                    const data = await res.json();
                    // data.Code  === 200
                    if (data?.Data === true) {
                        localStorage.setItem('From', location.href);
                        localStorage.setItem('LoginType', 'CShufan');
                        this.codeLogin(query);
                    } else {
                        this.jumpAuth('Shufan');
                    }
                } catch (error) {
                    this.jumpAuth('Shufan');
                    console.error('CheckExtendToken: ', error);
                }
            } else {
                this.jumpAuth('Shufan');
            }
        },
        initShufan() {
            const { search } = location;
            const query = queryString.parse(search);
            const { code, userName, userId } = query;
            // 其他应用单点登录跳转
            if (this.compType === 'pass')
                return;
            if (this.compType === 'cb')
                return;
            if (userName && userId && code)
                return;

            // 新增逻辑： 1 检查当前页面是否存在 authorization
            // 2 检查sf_token
            // 存在就不再跳转 直接转成authorization
            // 不存在再跳转数帆登录页
            this.dealFreesassLoginDebounce(query);
        },

        jumpSuccess(token) {
            cookie.set({ authorization: token });
            this.$emit('success', {
                Authorization: token,
            });
            if (window.appVue) {
                location.href = this.indexPage;
            }
        },
        //  处理跳转返回的 code
        async codeLogin(query) {
            const { code, userName, redirect, userId, refreshtoken, authorizationExpiredRefreshTime, scene } = query;
            // 其他应用单点登录跳转
            if (userName && userId && code) {
                const cookieUserInfo = parseToken(code);
                if (cookieUserInfo.AuthTypeTokenExpire) {
                    const day = +cookieUserInfo.AuthTypeTokenExpire / 60 / 60 / 24;
                    tokenMethod.set(code, day);
                } else {
                    tokenMethod.set(code);
                }
                if (refreshtoken) {
                    refreshtokenStorage.set(refreshtoken)
                }
                if (authorizationExpiredRefreshTime) {
                    authorizationExpiredTime.set(authorizationExpiredRefreshTime)
                }
                localStorage.setItem('LoginType', 'Other');
                cookie.set({ LoginType: 'Other' });
                location.href = redirect ? decodeURIComponent(redirect) : `/`;
            } else {
                // Auth 登录中转页
                const LoginType = localStorage.getItem('LoginType') || cookie.get('LoginType');
                const RedirectUri = localStorage.getItem('RedirectUri') || cookie.get('RedirectUri');
                let params = {
                    Code: code,
                    RedirectUri,
                    OAuthLoginType: 'Netease',
                    TenantName: getTenant(),
                    DomainName: NUIMS_DOMAIN_NAME,
                };
                const allLoginTypes = [...this.tabs, ...this.authTypes, ...this.redirectLoginTypes];
                const isplugin = allLoginTypes.find((item) => {
                    const value = item.value || item.name;
                    return value === LoginType.slice(1);
                })?.plugin;

                let currentService;
                if (this.low) {
                    currentService = localService;
                } else {
                    currentService = service;
                }
                let oAuthService = currentService.OauthLogin;
                if (isplugin) {
                    oAuthService = currentService.NuimsLogin;
                }

                if (this.low) {
                    oAuthService = localService.login;
                }
                if (LoginType === 'Github' || LoginType === 'CGithub') {
                    params.OAuthLoginType = 'Github';
                } else if (LoginType === 'Wechat' || LoginType === 'CWechat') {
                    params.OAuthLoginType = 'Wechat';
                } else if (LoginType === 'icbc_aam' || LoginType === 'Cicbc_aam') {
                    // 工行登陆
                    const { SSIAuth, SSISign } = query;
                    params = {
                        SsiAuth: SSIAuth,
                        SsiSign: SSISign,
                        LoginType: 'icbc_aam',
                        TenantName: getTenant(),
                        DomainName: NUIMS_DOMAIN_NAME,
                    };
                }

                const context = {
                    low: this.low,
                    query: queryString.parse(location.search),
                    loginType: LoginType.slice(1),
                    queryString,
                };
                const callback = (paramsCb) => {
                    if (paramsCb) {
                        params = { ...params, ...paramsCb };
                    } else {
                        // location.href = decodeURIComponent(authObj.toString());
                    }
                };
                usePlugin('codeLogin', context, callback);
                try {
                    let res;
                    if (this.low) {
                        // 请求制品的接口
                        res = await oAuthService({
                            data: {
                                ...params,
                                LoginType: LoginType.slice(1),
                            },
                            headers: {
                                'x-proxy-source': (this.scene || scene) === 'LowcodePlatform' ? 'low-code' : '',
                            },
                        });
                        res = res.data;
                    } else if (isplugin) {
                        const pluginParams = {
                            url: this.nuimsUrl,
                            body: {
                                ...params,
                                Code: code,
                                RedirectUri,
                                LoginType: LoginType.slice(1),
                                TenantName: this.tenantName || getTenant(),
                                DomainName: NUIMS_DOMAIN_NAME,
                            },
                        };
                        res = await oAuthService(pluginParams);
                        res = res.data;
                    } else {
                        res = await oAuthService({
                            url: this.nuimsUrl,
                            body: params,
                        });
                    }

                    const { Data } = getObj(res);
                    const { UserName, UserId, userName, userId } = getObj(Data);
                    let href;
                    // 登录组件通过 Auth 登录中转页
                    const pluginList = ['Github', 'Wechat', 'icbc_aam',
                                        'icbc_aam', 'Taikang', 'Shufan'];
                    if (concatPluginList(pluginList).includes(LoginType.slice(1))) {
                        href = decodeURIComponent(
                            redirect || localStorage.getItem('From') || cookie.get('From'),
                        );
                        const Token = tokenMethod.get();
                        let refreshPart = ''
                        const refreshtoken = refreshtokenStorage.get()
                        if (refreshtoken) {
                            refreshPart += `&refreshtoken=${refreshtoken}`
                        }
                        const authorizationExpiredRefreshTime = authorizationExpiredTime.get()
                        if (authorizationExpiredRefreshTime) {
                            refreshPart += `&authorizationExpiredRefreshTime=${authorizationExpiredRefreshTime}`
                        }
                        const Back = `${href}${
                            href.includes('?') ? '&' : '?'
                        }userName=${UserName || userName}&userId=${UserId || userId}&code=${Token}${refreshPart}`;
                        href = Back;
                    } else {
                        href = redirect ? decodeURIComponent(redirect) : `${origin}`;
                    }
                    // eslint-disable-next-line require-atomic-updates
                    location.href = href;
                } catch (error) {
                    console.error('error: ', error);
                    const { message } = error || {};
                    if (message && message.includes('The user is not bound')) {
                        location.href = '/403';
                    } else {
                        if (LoginType?.slice(1) === 'Shufan') {
                            this.jumpAuth('Shufan');
                        } else {
                            location.reload();
                        }
                        this.$toast.error(message);
                    }
                }
            }
        },
        defaultFocus() {
            if (this.tabs.length > 0) {
                this.account.LoginType = this.tabs[0].value;
            } else {
                this.hasTabLoginTypes = false;
                if (this.authTypes.length === 1) {
                    // 当有且仅有一种 Auth 登录时，直接登录
                    this.jumpAuth(this.authTypes[0].value);
                } else if (this.authTypes.length === 0) {
                    this.showNoneTips = true;
                }
            }
        },
        rmErrMsg() {
            this.errMsg = '';
        },
        async login() {
            let errMsg;
            try {
                errMsg = '';
                await this.$refs.accValidator.validate();
                await this.$refs.pwdValidator.validate();
                const { AccountId, AccountPassword, LoginType } = this.account;

                // const context = {
                //     low: this.low,
                //     cookie,
                //     loginType: LoginType,
                //     fnEnv: this.fnEnv,
                //     AccountId,
                //     AccountPassword,
                // };
                // let isEnd = false
                // const callback = (hrefcb) => {
                //     if (hrefcb) {
                //         isEnd = hrefcb;
                //     } else {
                //     }
                // };

                // usePlugin('login', context, callback);

                let res;

                if (this.low) {
                    const params = {
                        url: `/api/system/login`,
                        data: {
                            UserName: AccountId,
                            Password: AES_ECB_encrypt(AccountPassword),
                            LoginType,
                            TenantName: this.tenantName,
                        },
                        headers: {
                            Env: this.env,
                        },
                    };

                    res = await localService.login(params);
                    const { authorization } = res.headers;
                    const { userName, userId, ...rest } = getObj(getObj(res).data).Data; // 制品返回的登录信息的结构
                    cookie.set({ authorization });
                    if (res.headers?.refreshtoken) {
                        refreshtokenStorage.set(res.headers?.refreshtoken)
                    }
                    if (res?.headers?.['max-age']) {
                        let maxAge = Number(res?.headers['max-age'] || 0)
                        if (maxAge > 200) {
                            maxAge -= 200
                        }
                        const expiredTime = Date.parse(new Date()) / 1000 + maxAge
                        authorizationExpiredTime.set(expiredTime)
                    }
                    this.$emit('success', { Authorization: authorization, UserName: userName, UserId: userId, LoginType, ...rest });
                    if (window.appVue && !this.$listeners.success) {
                        location.href = this.indexPage;
                    }
                } else {
                    const params = {
                        url: this.nuimsUrl,
                        body: {
                            UserName: AccountId,
                            Password: AES_ECB_encrypt(AccountPassword),
                            LoginType,
                            TenantName: this.tenantName || getTenant(),
                            DomainName: NUIMS_DOMAIN_NAME,
                        },
                    };
                    if (LoginType === 'TZBank' && location.href.includes('tzbank.ins')) {
                        params.body.JobNo = params.body.UserName;
                        cookie.set = (data = {}, exdays = 1) => {
                            const d = new Date();
                            d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
                            const expires = d.toGMTString();
                            Object.keys(data).forEach((key) => {
                                const value = data[key];
                                if (key && value) {
                                    document.cookie = `${key}=${encodeURIComponent(value)}; expires=${expires}; path=/;Domain=tzbank.ins`;
                                }
                            });
                        };
                    }
                    const loginParams = {
                        url: this.nuimsUrl,
                        data: {
                            UserName: AccountId,
                            Password: AES_ECB_encrypt(AccountPassword),
                            LoginType,
                            TenantName: this.tenantName || getTenant(),
                            DomainName: NUIMS_DOMAIN_NAME,
                        },
                    };
                    if (this.env) {
                        params.headers = {
                            Env: this.env,
                        };
                        loginParams.headers = {
                            Env: this.env,
                        };
                    }
                    const isPermissionCenter = this.scene === 'PermissionCenter';
                    if (isPermissionCenter) {
                        params.headers = {
                            ...params.headers,
                            'x-request-source': 'low-code-front',
                        };
                        loginParams.headers = {
                            ...loginParams.headers,
                            'x-request-source': 'low-code-front',
                        };
                    }
                    const isLowcodePlatform = this.scene === 'LowcodePlatform'
                    if (isLowcodePlatform) {
                        params.headers = {
                            ...params.headers,
                            'x-proxy-source': 'low-code',
                        };
                        loginParams.headers = {
                            ...loginParams.headers,
                            'x-proxy-source': 'low-code',
                        };
                    }
                    const isplugin = this.tabs.find((item) => item.value === LoginType)?.plugin;
                    const loginApi = isplugin ? service.NuimsLogin : service.login;
                    res = await loginApi(isplugin ? params : loginParams);
                    const { authorization } = res.headers;
                    const { UserName, UserId } = res.data.Data;
                    cookie.set({ authorization });
                    if (res.headers?.refreshtoken) {
                        refreshtokenStorage.set(res.headers?.refreshtoken)
                    }
                    if (res?.headers?.['max-age']) {
                        let maxAge = Number(res?.headers['max-age'] || 0)
                        if (maxAge > 200) {
                            maxAge -= 200
                        }
                        const expiredTime = Date.parse(new Date()) / 1000 + maxAge
                        authorizationExpiredTime.set(expiredTime)
                    }
                    this.$emit('success', { Authorization: authorization, UserName, UserId, LoginType, AccountId, AccountPassword });
                    if (window.appVue && !this.$listeners.success) {
                        location.href = this.indexPage;
                    }
                }
            } catch (error) {
                errMsg = error && error.message;
                if (error && error?.status === 404) {
                    errMsg = '接口不存在！';
                }
                if (
                    errMsg
                    === '参数错误，请检查如下请求参数: UserName or Password'
                ) {
                    errMsg = '请输入正确的账号或密码';
                }
                this.$toast.error(errMsg);
                this.$emit('error', { Message: errMsg || 'login error' });
            }
            this.errMsg = errMsg;
        },
        // Auth 登录
        jumpAuth(type) {
            const { search } = location;
            const allLoginTypes = [...this.tabs, ...this.authTypes, ...this.redirectLoginTypes];
            const isplugin = allLoginTypes.find((item) => item.value === type)?.plugin;
            this.low = this.low || isplugin;
            let jumpHref;
            if (concatPluginList(['Taikang', 'Shufan', 'icbc_aam']).includes(type)) {
                this.low = false;
                if (this.isProductApp && window?.appInfo?.hasUserCenter) {
                    this.low = true;
                }
            }
            // if (type === 'Xuetong') {
            //     this.low = false;
            // }
            // 制品下沉
            if (this.low) {
                // 下沉式在当前环境设置值，集中式不需要设置
                localStorage.setItem('compType', 'pass');
            }
            const { redirect = encodeURIComponent(location.href), scene } = queryString.parse(search);
            // 非下沉模式，需要跳转到 nuims 的集中登录
            let fnPassSrc = this.passSrc;
            if (type === 'Shufan') {
                fnPassSrc = this.passSrc.replace(/http(s)?:\/\/(dev\.)?/, 'https://');
                const url = `${this.low ? location.href : `${fnPassSrc}/pass`}?redirect=${redirect}&loginType=C${type}&env=${this.fnEnv}&scene=${this.scene || scene}`;
                location.href = url;
                return;
            }
            // 工行生产环境去掉默认 defaulttenant
            if (type === 'icbc_aam' && !fnPassSrc.includes('dev.defaulttenant.')) {
                fnPassSrc = fnPassSrc.replace('defaulttenant.', '');
            }
            if (type === 'Netease') {
                fnPassSrc = fnPassSrc.replace(/http(s)?:\/\/?/, 'http://');
            }
            const context = {
                query: queryString.parse(search),
                passSrc: this.passSrc,
                low: this.low,
                redirect,
                loginType: type,
                fnEnv: this.fnEnv,
                nuimsUrl: this.nuimsUrl,
            };
            const callback = (hrefcb) => {
                if (hrefcb) {
                    jumpHref = hrefcb;
                } else {
                    jumpHref = `${this.low ? location.href : `${fnPassSrc}/pass`}?redirect=${redirect}&loginType=C${type}&scene=${this.scene || scene}`;
                }
            };

            usePlugin('beforeJump', context, callback);
            location.href = jumpHref || `${this.low ? location.href : `${fnPassSrc}/pass`}?redirect=${redirect}&loginType=C${type}&scene=${this.scene || scene}`;
        },
        async getTenantConfigForPass() {
            // 跳转回来，页面刷新，这时候需要请求一次配置
            let AuthConfigMap = {};
            let res;
            if (this.low) {
                res = await localService.getTenantLoginTypes({
                });
                AuthConfigMap = getObj(getObj(getObj(res).data).Data);
            } else {
                res = await service.getTenantLoginTypes({
                    url: this.nuimsUrl,
                    params: { TenantName: this.tenantName || getTenant() },
                });
                getArr(getObj(res).data.Data).forEach((d) => {
                    const { LoginType } = getObj(d);
                    AuthConfigMap[LoginType] = [d];
                });
            }
            this.AuthConfigMap = AuthConfigMap;
        },
        async jumpAuthPass() {
            const allLoginTypes = [...this.tabs, ...this.authTypes, ...this.redirectLoginTypes];
            // 因为跳转回来之后，组件会刷新，所以配置信息需要载请求一次
            await this.getTenantConfigForPass();
            const { origin, search, pathname } = location;
            const query = queryString.parse(search);
            const { redirect, loginType } = query;
            const isplugin = allLoginTypes.find((item) => item.value === loginType.slice(1))?.plugin;
            if (redirect) {
                localStorage.setItem('From', redirect);
                cookie.set({ From: redirect });
            }
            //  || loginType.slice(1) === 'Shufan'
            if (this.low || isplugin) {
                localStorage.setItem('compType', 'cb');
            }
            const redirectUri = `${origin}${pathname}?redirect=${redirect}`;
            localStorage.setItem('LoginType', loginType);
            localStorage.setItem('RedirectUri', redirectUri);
            cookie.set({ RedirectUri: redirectUri, LoginType: loginType });

            let authUrl;
            switch (loginType) {
                case 'Github':
                case 'CGithub':
                    authUrl = `https://github.com/login/oauth/authorize?${queryString.stringify({
                        response_type: 'code',
                        scope: 'user:email',
                        client_id: this.AuthConfigMap.Github,
                        redirect_uri: redirectUri,
                    })}`;
                    break;
                case 'Wechat':
                case 'CWechat':
                    authUrl = `https://open.weixin.qq.com/connect/qrconnect?${queryString.stringify({
                        response_type: 'code',
                        scope: 'snsapi_login',
                        appid: this.AuthConfigMap.Wechat,
                        redirect_uri: redirectUri,
                    })}`;
                    break;
                case 'icbc_aam':
                case 'Cicbc_aam':
                    // eslint-disable-next-line no-case-declarations
                    const { icbc_aamAAMUrl, Version, Style, Op, OpMode, Lang, ServiceName } = this.AuthConfigMap.icbc_aam;
                    authUrl = `${icbc_aamAAMUrl}?${queryString.stringify({
                        SERVICENAME: ServiceName,
                        SERVICEURL: redirectUri,
                        VERSION: Version,
                        STYLE: Style,
                        OP: Op,
                        OPMode: OpMode,
                        lang: Lang,
                    })}`;
                    break;
            }
            if (this.isProductApp && this.low) {
                authUrl = this.AuthConfigMap[loginType.slice(1)].config.authPageUrl;
            } else {
                authUrl = this.AuthConfigMap[loginType.slice(1)][0]?.extendProperties?.authPageUrl;
            }

            const authObj = new URL(authUrl);
            const obj = queryString.parse(authObj.search);
            obj.redirect_uri = redirectUri;
            authObj.search = queryString.stringify(obj);
            if (loginType.slice(1) === 'icbc_aam') {
                obj.SERVICEURL = redirectUri;
                authObj.search = queryString.stringify(obj);
                location.href = authObj.toString();
            } else if (loginType.slice(1) === 'Shufan') {
                location.href = `${authUrl}${encodeURIComponent(redirectUri)}`;
                return;
            }
            let jumpPassUrl;
            const context = {
                loginType: loginType.slice(1),
                low: this.low,
                nuimsUrl: this.nuimsUrl,
                redirectUri,
                query,
                queryString,
                authUrl,
                authUrlObj: authObj,
                authSearch: obj,
            };
            const callback = (hrefcb) => {
                if (hrefcb) {
                    jumpPassUrl = hrefcb;
                } else {
                    jumpPassUrl = decodeURIComponent(authObj.toString());
                }
            };
            usePlugin('jumpPass', context, callback);

            if (jumpPassUrl !== 'unuse') {
                location.href = jumpPassUrl || decodeURIComponent(authObj.toString());
            }
        },
    },
};
</script>

