// 已转为 remoteJSurl
class Netease {
    constructor() {
        this.name = 'Netease';
        this.category = 'Sub';
    }

    beforeJump(context, callback) {
        // const { } = context;
        callback();
    }

    jumpPass(context, callback) {
        const { authUrl, redirectUri, queryString } = context;
        const authObj = new URL(authUrl);
        const obj = queryString.parse(authObj.search);
        obj.redirect_uri = redirectUri;
        authObj.search = queryString.stringify(obj);
        const href = authObj.toString();
        callback(href);
    }

    codeLogin(context, callback) {
        callback();
    }

    nuimsJumpPass(context, callback) {
        const { redirectUri, AuthConfigMap } = context;
        const { extendProperties } = AuthConfigMap.Netease;
        const authUrl = `${extendProperties.authPageUrl}&redirect_uri=${encodeURIComponent(redirectUri)}`;
        callback(authUrl);
    }

    nuimsCodeLogin(context, callback) {
        const { RedirectUri, query } = context;
        const paramsCb = {
            body: {
                Code: query.code,
                RedirectUri,
                LoginType: 'Netease',
            },
        };
        // callback 第二个参数是指定接口调用： 默认调用nuimsLogin接口
        callback(paramsCb);
    }

    // afterLogin(context, callback) {
    //     const beforeLoginHref = JSON.parse(localStorage.getItem('beforeLogin'));
    //     callback(beforeLoginHref?.pathname);
    // }
}
window.Netease = Netease;
export default Netease;
