import axios from 'axios';
import token from './token';
import refreshtokenStorage from './refreshtokenStorage';
import authorizationExpiredTime from './authorizationExpiredTime';
import Vue from 'vue';

export const apiVersion = '2020-06-01';

export const formatContentType = (contentType, data) => {
    const map = {
        'application/x-www-form-urlencoded'(data) {
            return queryString.stringify(data);
        },
    };
    return (map[contentType] && map[contentType](data)) || data;
};

// 调整请求路径
const adjustPathWithSysPrefixPath = (config) => {
    const { url, ...others } = config || {};
    const newConfig = {
        url,
        ...others,
    };
    const sysPrefixPath = window.appInfo?.sysPrefixPath;
    if (sysPrefixPath && newConfig) {
        const url = newConfig?.url;
        if (url && url.startsWith('/')) {
            newConfig.url = sysPrefixPath + url;
        }
    }
    return newConfig;
};

export function request(config) {
    const newConfig = adjustPathWithSysPrefixPath(config);

    if(Vue.prototype.$formatMicroFrontUrl)
        newConfig.url = Vue.prototype.$formatMicroFrontUrl(newConfig.url);

    return axios(newConfig)
        .then((res = {}) => {
            const { status, data = {}, headers = {} } = res;
            if ((status + '').startsWith('2')) {
                const { Authorization, authorization } = headers;
                if (Authorization || authorization) {
                    token.set(Authorization || authorization);
                }
                if (headers?.refreshtoken) {
                    refreshtokenStorage.set(headers?.refreshtoken)
                }
                if (headers?.['max-age']) {
                    let maxAge = Number(headers['max-age'] || 0)
                    if (maxAge > 200) {
                        maxAge -= 200
                    }
                    const expiredTime = Date.parse(new Date()) / 1000 + maxAge
                    authorizationExpiredTime.set(expiredTime)
                }

                return { headers, data };
            }
        })
        .catch((err) => {
            if (err.request) {
                const { responseText, status } = err.request;
                let message;
                try {
                    message = JSON.parse(responseText).Message;
                } catch (error) {
                    message = responseText;
                }
                err = { status, message };
            }
            throw err;
        });
}
