async function initYunzao(context, callback) {
    const { low, query, TenantName, DomainName, service, localService, nuimsUrl, cookieUitl: cookie } = context;
    const { yunzaohref, token } = query;
    let res;
    if (low) {
        const params = {
            data: {
                LoginType: 'Yunzao',
                TenantName,
                DomainName,
                token,
            },
        };

        res = await localService.login(params);
    } else {
        const params = {
            url: nuimsUrl,
            body: {
                LoginType: 'Yunzao',
                TenantName,
                DomainName,
                token,
            },
        };
        res = await service.NuimsLogin(params);
    }
    const { UserName, UserId, userName, userId, EnterpriseAuthInfo } = res.data.Data;
    const { authorization } = res.headers;
    const userInfo = {
        authorization,
        UserName: UserName || userName,
        UserId: UserId || userId,
        EnterpriseAuthInfo,
    };
    const localYunzaohref = localStorage.getItem('yunzaohref');
    const beforeLoginHref = JSON.parse(localStorage.getItem('beforeLogin'));
    const href = beforeLoginHref?.pathname || localYunzaohref || yunzaohref || '/';
    callback(userInfo, href);
}
class Yunzao {
    constructor() {
        this.name = 'Yunzao';
        this.category = 'Sub';
    }

    async initDirectProduct(context, callback) {
        await initYunzao(context, callback);
    }
    // afterLogin(context, callback) {
    //     const beforeLoginHref = JSON.parse(localStorage.getItem('beforeLogin'));
    //     callback(beforeLoginHref.pathname);
    // }
}
window.Yunzao = Yunzao;
export default Yunzao;

