<script>
import queryString from 'query-string';
import localStorage from '../localStorage';
import cookie from '../cookie';
import { getArr, getTenant, getObj } from '../util';
import service from '../service';
import { usePlugin, loadOneRemotePlugin } from '../plugin/index';

export default {
    name: 'app',
    props: {
        nuimsUrl: {
            type: String,
            default: '',
        },
        tenantName: {
            type: String,
            default: '',
        },
        env: {
            type: String,
            default: '',
        },
        scene: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            AuthConfigMap: {},
        };
    },
    methods: {
        async oldMounted() {
            await this.getTenantConfig();
            this.jumpAuth();
        },
        async getTenantConfig() {
            const { search } = location;
            const { env, loginType } = queryString.parse(search);
            const AuthConfigMap = {};
            const params = {
                url: this.nuimsUrl,
                params: { TenantName: this.tenantName || getTenant() },
            };
            if (env || this.env) {
                params.headers = {
                    Env: env || this.env,
                };
            }
            const res = await service.getTenantLoginTypes(params);
            const getResData = (res) => getArr(getObj(getObj(res).data).Data);
            const item = getResData(res).find((it) => it.LoginType === loginType.slice(1));
            await loadOneRemotePlugin(item.LoginType, item.extendProperties.authJsUrl);
            getResData(res).forEach((d) => {
                const fullConfig = getObj(d);
                const { LoginType } = fullConfig;
                AuthConfigMap[LoginType] = fullConfig;
            });
            this.AuthConfigMap = AuthConfigMap;
        },
        jumpAuth() {
            const { origin, search } = location;
            const { redirect, loginType, env, scene } = queryString.parse(search);
            if (redirect) {
                localStorage.set('From', redirect);
                cookie.set({ From: redirect });
            }
            let redirectUri = `${origin}/cb?redirect=${redirect}&scene=${this.scene || scene}`;
            localStorage.set('LoginType', loginType);
            localStorage.set('RedirectUri', redirectUri);
            cookie.set({ RedirectUri: redirectUri, LoginType: loginType });

            let authUrl;

            switch (loginType) {
                case 'Github':
                case 'CGithub':
                    authUrl = `https://github.com/login/oauth/authorize?${queryString.stringify({
                        response_type: 'code',
                        scope: 'user:email',
                        client_id: this.AuthConfigMap.Github.AppKey,
                        redirect_uri: redirectUri,
                    })}`;
                    break;
                case 'Wechat':
                case 'CWechat':
                    authUrl = `https://open.weixin.qq.com/connect/qrconnect?${queryString.stringify({
                        response_type: 'code',
                        scope: 'snsapi_login',
                        appid: this.AuthConfigMap.Wechat.AppKey,
                        redirect_uri: redirectUri,
                    })}`;
                    break;
                case 'Taikang':
                case 'CTaikang':
                    // eslint-disable-next-line no-case-declarations
                    const { extendProperties: extProps } = this.AuthConfigMap.Taikang;
                    authUrl = `${extProps.auth_url || extProps.authPageUrl}&redirect_uri=${encodeURIComponent(redirectUri)}`;
                    break;
                case 'icbc_aam':
                case 'Cicbc_aam':
                    // eslint-disable-next-line no-case-declarations
                    const { extendProperties: idbcExtProps } = this.AuthConfigMap.icbc_aam;
                    authUrl = `${idbcExtProps.authPageUrl}&SERVICEURL=${encodeURIComponent(redirectUri)}`;
                    break;
                case 'Shufan':
                case 'CShufan':
                    // eslint-disable-next-line no-case-declarations
                    const { extendProperties: shufanExtProps } = this.AuthConfigMap.Shufan;
                    redirectUri = env ? `${redirectUri}&env=${env}` : redirectUri;
                    localStorage.set('RedirectUri', redirectUri);
                    cookie.set({ RedirectUri: redirectUri });
                    authUrl = `${shufanExtProps.authPageUrl}${encodeURIComponent(redirectUri)}`;
                    break;
            }

            const context = {
                nuimsUrl: this.nuimsUrl,
                AuthConfigMap: this.AuthConfigMap,
                authUrl: this.AuthConfigMap[loginType.slice(1)]?.extendProperties?.authPageUrl || '',
                loginType: loginType.slice(1),
                query: queryString.parse(search),
                redirectUri,
                queryString,
                cookie,
            };
            const callback = (authUrlCb) => {
                if (authUrlCb) {
                    authUrl = authUrlCb;
                    // 未插件化的类型 不做改动
                } else if (!authUrl) {
                    const { extendProperties } = this.AuthConfigMap[loginType.slice(1)];
                    const authObj = new URL(extendProperties.authPageUrl);
                    const obj = queryString.parse(authObj.search);
                    obj.redirect_uri = redirectUri;
                    authObj.search = queryString.stringify(obj);
                    authUrl = authObj.toString();
                }
            };
            usePlugin('nuimsJumpPass', context, callback);
            if (authUrl !== 'unuse') {
                location.href = authUrl;
            }
        },
    },
    render(h) {
        return h('div', '');
    },
};
</script>
